/**
 * Using Rails-like standard naming convention for endpoints.
 * GET     /api/schms              ->  index
 * POST    /api/schms              ->  create
 * GET     /api/schms/:id          ->  show
 * PUT     /api/schms/:id          ->  update
 * DELETE  /api/schms/:id          ->  destroy
 */

'use strict';

import _ from 'lodash';
import Schm from './schm.model';
import q from 'q';
var Stream = require('stream').Stream;

function respondWithResult(res, statusCode) {
    statusCode = statusCode || 200;
    return function(entity) {
        if (entity) {
            res.status(statusCode).json(entity);
        }
    };
}


function ArrayFormatter() {
    Stream.call(this);
    this.writable = true;
    this._done = false;
}

ArrayFormatter.prototype = Stream.prototype;

ArrayFormatter.prototype.write = function(doc) {
    if (!this._hasWritten) {
        this._hasWritten = true;

        // open an object literal / array string along with the doc
        this.emit('data', '[' + JSON.stringify(doc));

    } else {
        this.emit('data', ',' + JSON.stringify(doc));
    }

    return true;
}

ArrayFormatter.prototype.end =
    ArrayFormatter.prototype.destroy = function() {
        if (this._done) return;
        this._done = true;

        // close the object literal / array
        this.emit('data', ']');
        // done
        this.emit('end');
    }

export function stream(req, res) {
    var query = {};

    if (checkParam(req.query.type, 'string')) {
        query.type = req.query.type;
        //console.log('schm')
    }

    if (checkParam(req.query.name, 'string')) {
        query.name = req.query.name;
        //console.log('schm')
    }

    if (checkParam(req.query.filter, 'filter')) {
        query["$and"] = [];

        var filter = JSON.parse(req.query.filter);
        for (var i = 0; i < filter.length; i++) {
            var p = { attributes: {} };
            p["attributes"]["$elemMatch"] = {};
            p["attributes"]["$elemMatch"]['id'] = filter[i].key;
            p["attributes"]["$elemMatch"][filter[i].datatype] = filter[i].value;
            query["$and"].push(p);
        }
    }

    Schm.find(query).cursor().pipe(new ArrayFormatter()).pipe(res);

}
var AggregateParser = require("../record/services/aggregation.oidParser").AggParser;

export function aggregate(req, res) {
    var query;


    var aP = new AggregateParser(req.query.query);
    query = aP.parse();

    return Schm.aggregate(query).exec()
        .then(respondWithResult(res, 200))
        .catch(handleError(res));
}

function saveUpdates(updates) {
    return function(entity) {
        var updated = _.extend(entity, updates);
        return updated.save()
            .then(updated => {
                return updated;
            });
    };
}

function removeEntity(res) {
    return function(entity) {
        if (entity) {
            return entity.remove()
                .then(() => {
                    res.status(204).end();
                });
        }
    };
}

function handleEntityNotFound(res) {
    return function(entity) {
        if (!entity) {
            res.status(404).end();
            return null;
        }
        return entity;
    };
}

function handleError(res, statusCode) {
    statusCode = statusCode || 500;
    return function(err) {
        res.status(statusCode).send(err);
    };
}

function checkParam(param, dataType) {

    if (param === null) { return false; }
    var response = false;

    if (dataType === 'string') {
        if (typeof param === 'string' && param.length > 0) {
            response = true;
        }
    }

    if (dataType === 'number') {
        //console.log('chequea numero')
        if (typeof param === 'number') {
            response = true;
        }
        if (typeof param === 'string') {

            if (/^\d*$/.test(param)) {
                //console.log('es  numero')
                response = true;
            }
        }
    }

    if (dataType === 'objectId') {
        if (/^[0-9a-f]{24}$/i.test(param)) {
            response = true;
        }
    }

    //filtro de registros
    if (dataType === 'filter') {
        //checkeando si hay errores en el parseo a JSON
        try {
            var arr = JSON.parse(param);
            //check if is an Array and if is empty
            if (arr.length) {
                // verificando si los obj dentro del array tiene las propiedades key, datatype y value
                var isValid = true;
                for (var index = 0; index < arr.length; index++) {
                    if (arr[index].key === null || arr[index].value === null || arr[index].datatype === null) {
                        isValid = false;
                    }
                }
                response = isValid;
            }

        } catch (err) {
            response = false;
            console.log('invalid JSON')
        }
    }

    return response;
}

// Gets a list of Schms
export function index(req, res) {
    console.log(req.query.items);
    var items = 30;
    if (checkParam(req.query.items, 'number')) {
        items = parseInt(req.query.items);
    }

    var page = req.query.page || 1;
    // checking the query data types
    if (!checkParam(page, 'number') || page === "0") {
        return respondWithResult(res, 500)({ Error: 'El par치metro page no es v치lido' });
    }
    if (!checkParam(items, 'number')) {
        //return respondWithResult(res, 500)({Error:'El par치metro items no es v치lido'});
    }

    var query = {};
    //filtrar por schm
    if (checkParam(req.query.type, 'string')) {
        query.type = req.query.type;
        //console.log('schm')
    }

    if (checkParam(req.query.filter, 'filter')) {
        query["$and"] = [];

        var filter = JSON.parse(req.query.filter);
        for (var i = 0; i < filter.length; i++) {
            var p = { attributes: {} };
            p["attributes"]["$elemMatch"] = {};
            p["attributes"]["$elemMatch"]['id'] = filter[i].key;
            p["attributes"]["$elemMatch"][filter[i].datatype] = filter[i].value;
            query["$and"].push(p);
        }
        //console.log(filter);
    }
    console.log(JSON.stringify(query))

    q.all([
        Schm.find(query).count().exec(),
        Schm.find(query).skip(items * (page - 1)).limit(items)
    ]).spread((count, data) => {
        respondWithResult(res)({
            page: parseInt(page),
            pages: Math.ceil(count / items),
            length: data.length,
            totalLength: count,
            items: data
        });
    }).fail(handleError(res));
    /*
    return Schm.find().exec()
      .then(respondWithResult(res))
      .catch(handleError(res));
    */
}

// function helper
var findValueByVarHelper = function(array, key, value, target) {
    var self = this;
    if (!Array.isArray(array)) { return null; }

    var index = array.map(x => x[key]).indexOf(value);

    if (index !== -1) {
        if (target === undefined) {
            return array[index];
        } else {
            return array[index][target];
        }
    } else {
        return null;
    }
}
export function fullSchm(schmId) {
    console.log("El schema id es: " + schmId);
    var firstCall = function(s) {
        var query = { type: "schmAttrInputConf", attributes: { "$elemMatch": { id: "schema", reference: schmId } } }
        Schm.find({ type: "schmAttrInputConf", attributes: { "$elemMatch": { id: "schema", reference: "57c42f2fc8307cd5b82f4484" } } }).exec().then(function(f) {
            console.log("Resultado de la query es: ")
            console.log(f)
        })
        return q.all([
            Schm.find(query).exec(),
            Schm.findById(schmId),

        ]).spread((schmAttrInputConf, schm) => {
            console.log("El schm id es : " + schm._id + " y la cantidad de schmAttrInputConf es: " + schmAttrInputConf.length);
            let index = schm.attributes.map(x => x.id).indexOf("attributes");
            var AttrList = [];
            if (index !== -1) { AttrList = schm.attributes[index].list }
            return {
                schema: schm,
                schmAttrInputConf: schmAttrInputConf,
                list: AttrList
            }

        })

    }
    var secondCall = function(d) {
        var query = { "$or": [] };
        for (var i = 0; i < d.list.length; i++) {
            query["$or"].push({ type: "attrInputConf", attributes: { "$elemMatch": { id: "attribute", reference: d.list[i] } } });
        }
        return Schm.find(query).then(function(s) {
            d.attrInputConf = s;
            return d
        })
    }
    var thirdCall = function(d) {
        var query = { "$or": [] };
        for (var i = 0; i < d.list.length; i++) {
            query["$or"].push({ _id: d.list[i] });
        }
        return Schm.find(query).then(function(s) {
            d.attributes = s;
            return d
        })
    }
    var forthCall = function(d) {
        var inputRefs = d.attributes.map(x => findValueByVarHelper(x.attributes, "id", "input", "reference"));
        //console.log(inputRefs)
        var queryInputs = { "$or": [] };
        for (var i = 0; i < inputRefs.length; i++) {
            queryInputs["$or"].push({ _id: inputRefs[i] });
        }
        //console.log(queryInputs);
        return Schm.find(queryInputs).then(function(s) {
            d.inputs = s;
            return d
        })
    }

    var concatCall = function(d) {
        var concat = [d.schema];
        concat = concat.concat(d.attributes);
        concat = concat.concat(d.schmAttrInputConf);
        concat = concat.concat(d.attrInputConf);
        concat = concat.concat(d.inputs);
        return concat;
    }

    return q.fcall(x => true)
        .then(firstCall)
        .then(secondCall)
        .then(thirdCall)
        .then(forthCall)
        .then(concatCall)
}


// Gets a single Schm from the DB
export function show(req, res) {
    if (req.query.record_only === "true") {
        return Schm.findById(req.params.id).exec()
            .then(handleEntityNotFound(res))
            .then(respondWithResult(res))
            .catch(handleError(res));
    }

    fullSchm(req.params.id)
        .then(function(d) {
            respondWithResult(res)(d);
        })
        .fail(handleError(res));

}



// Creates a new Schm in the DB
export function create(req, res) {
    return Schm.create(req.body)
        .then(respondWithResult(res, 201))
        .catch(handleError(res));
}

// Updates an existing Schm in the DB
export function update(req, res) {
    if (req.body._id) {
        delete req.body._id;
    }
    return Schm.findById(req.params.id).exec()
        .then(handleEntityNotFound(res))
        .then(saveUpdates(req.body))
        .then(respondWithResult(res))
        .catch(handleError(res));
}

// Deletes a Schm from the DB
export function destroy(req, res) {
    return Schm.findById(req.params.id).exec()
        .then(handleEntityNotFound(res))
        .then(removeEntity(res))
        .catch(handleError(res));
}

